import React, { Component } from "react";
import dynamic from "next/dynamic";

// Begin Component
// __________________________________________________________________________

type P5Wrapper = any;

const P5Wrapper: P5Wrapper = dynamic(import("react-p5-wrapper"), {
  loading: () => <p>Loading...</p>,
  ssr: false,
});

class P5Page extends Component<{}, any> {
  constructor(props) {
    super(props);

    this.state = {
      domLoaded: false,
      windowWidth: 0,
      windowHeight: 0,
    };
  }

  componentDidMount() {
    if (typeof window) {
      this.setState({
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight,
      });
    }
  }

  renderP5 = (filename: string) => {
    const sketch = require(`../../sketches/p5/${filename}`).default(
      this.state.windowWidth,
      this.state.windowHeight
    );

    return <P5Wrapper sketch={sketch} />;
  };

  render() {
    return <div className="container">{this.renderP5("glsl")}</div>;
  }
}

export default P5Page;
